eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true, sub: true, regexp: true */\r\n/*global define, brackets, graphite, window, PathUtils*/\r\ndefine(\'ext/ims/IMSLibWrapper\',[\r\n    \"module\",\r\n    \"jquery\",\r\n    \"underscore\",\r\n    \"backbone\",\r\n    \"config/environment\",\r\n    \"ext/ims/IMSLibLoader\",\r\n    \"ext/ims/IMSProxy\",\r\n    \"adobeid\",\r\n    \"ext/command/Commands\",\r\n    \"ext/models/BracketsPreferencesModel\",\r\n    \"ext/models/ExtensionMetaData\",\r\n    \"ext/CacheManager\",\r\n    \"ext/strings\"\r\n], function (module, $, _, Backbone, environment, IMSLibLoader, IMSProxy,\r\n    adobeid, Commands, BracketsPreferencesModel, ExtensionMetaData, CacheManager,\r\n    Strings) {\r\n    \"use strict\";\r\n    \r\n    var DefaultDialogs  = brackets.getModule(\"widgets/DefaultDialogs\"),\r\n        Dialogs         = brackets.getModule(\"widgets/Dialogs\"),\r\n        Menus           = brackets.getModule(\"command/Menus\"),\r\n        StringUtils     = brackets.getModule(\"utils/StringUtils\");\r\n\r\n    var RE_COOKIE_PAIR = /(\\S+)=(.*)/;\r\n\r\n    var imslib,\r\n        imsReady = false,\r\n        $adobeid = $(adobeid),\r\n        prefs = BracketsPreferencesModel.get(\"preferenceSystem\"),\r\n        cmdToggleAuth;\r\n\r\n    function loadIMS() {\r\n        if (imslib) {\r\n            return new $.Deferred().resolve(imslib).promise();\r\n        }\r\n\r\n        // getPromise is reentrant, prevents imslib from loading more than once\r\n        return IMSLibLoader.getPromise().done(function (hostedIMS) {\r\n            imslib = hostedIMS;\r\n        });\r\n    }\r\n    \r\n    var IMSLibWrapper = Backbone.Model.extend({\r\n        \r\n        initialize: function () {\r\n            var self = this,\r\n                wasSignedIn = false,\r\n                initStart = Date.now();\r\n\r\n            // Restore cookies and update proxy server\r\n            self._initializeCookie();\r\n            \r\n            $adobeid.on(\"ready\", function _onStateChange(event) {\r\n                if (event.type === \"ready\") {\r\n                    imsReady = true;\r\n\r\n                    // Enable menu item when imslib is ready\r\n                    cmdToggleAuth.setEnabled(true);\r\n\r\n                    graphite.events.trigger(\"ims-ready\", {\r\n                        duration: Date.now() - initStart\r\n                    });\r\n                }\r\n\r\n                // fire \"signIn\" and \"signOut\" events when imslib updates\r\n                // Note that the accessToken event is not included since profile must \r\n                // always fire after accessToken\r\n                $adobeid.on(\"profile accessTokenHasExpired\", _onStateChange);\r\n\r\n                var isSignedIn = self.isSignedInUser(),\r\n                    triggerEventName = (isSignedIn) ? \"signIn\" : \"signOut\";\r\n                \r\n                // Only fire event when signed in state changes\r\n                if (isSignedIn !== wasSignedIn) {\r\n                    var data = null;\r\n\r\n                    if (isSignedIn) {\r\n                        data = _.extend({}, imslib.getAccessTokenInfo(), imslib.getUserProfile());\r\n                        \r\n                        // Update command label\r\n                        cmdToggleAuth.setName(Strings.MENU_CC_SIGN_OUT);\r\n                    } else {\r\n                        // Clear cookies on signOut\r\n                        self._setCookie({});\r\n                        \r\n                        // Update command label\r\n                        cmdToggleAuth.setName(Strings.MENU_CC_SIGN_IN);\r\n                    }\r\n                    \r\n                    $(self).triggerHandler(triggerEventName, [data]);\r\n                    wasSignedIn = isSignedIn;\r\n                }\r\n\r\n                // These events imply user is online\r\n                self._handleNetworkChange(true);\r\n            });\r\n\r\n            // Log when access token expires\r\n            $adobeid.on(\"accessTokenHasExpired\", function (event) {\r\n                graphite.events.trigger(\"expired-access-token\");\r\n            });\r\n\r\n            // event handlers when popup window closes\r\n            $adobeid.on(\"getClientWindow\", function (event, promise) {\r\n                // disable the login menu while we try to sign in\r\n                cmdToggleAuth.setEnabled(false);\r\n\r\n                promise.always(function () {\r\n                    // renable login menu when dialog closes\r\n                    cmdToggleAuth.setEnabled(true);\r\n\r\n                    // internal _dialogClosed event\r\n                    $(self).triggerHandler(\"_dialogClosed\");\r\n                });\r\n            });\r\n\r\n            // Online/Offline handlers\r\n            $adobeid.on(\"error\", function (event, error) {\r\n                var isOnline = (error.name !== \"networkError\");\r\n                self._handleNetworkChange(isOnline);\r\n            });\r\n\r\n            window.addEventListener(\"online\", function (e) {\r\n                self._handleNetworkChange(true);\r\n            }, false);\r\n\r\n            window.addEventListener(\"offline\", function (e) {\r\n                self._handleNetworkChange(false);\r\n            }, false);\r\n\r\n            // FIXME navigator.onLine always returns true as of Brackets 44\r\n            self._handleNetworkChange(navigator.onLine);\r\n        },\r\n\r\n        _updateMetrics: function (isSignUp, imsCountProperty) {\r\n            var defaultParams = prefs.get(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS),\r\n                metricName = isSignUp ? \"signUp\" : \"signIn\",\r\n                prefName = \"ims.\" + metricName + \".\" + imsCountProperty,\r\n                count = defaultParams[prefName] + 1;\r\n\r\n            defaultParams[prefName] = count;\r\n\r\n            // Save prefs\r\n            prefs.set(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS, defaultParams);\r\n        },\r\n\r\n        _handleNetworkChange: function (isOnline) {\r\n            if (!isOnline) {\r\n                $(this).triggerHandler(\"networkError\");\r\n            }\r\n        },\r\n\r\n        _parseCookie: function (cookie) {\r\n            var result = {},\r\n                pairs = cookie.split(/; */),\r\n                match;\r\n\r\n            pairs.forEach(function (pair) {\r\n                match = pair.trim().match(RE_COOKIE_PAIR);\r\n\r\n                if (match) {\r\n                    result[match[1]] = decodeURIComponent(match[2]);\r\n                }\r\n            });\r\n\r\n            return result;\r\n        },\r\n\r\n        _setCookie: function (updatedCookie) {\r\n            var cookie = updatedCookie || prefs.get(BracketsPreferencesModel.IMS_COOKIE);\r\n\r\n            // Save updated cookie to preferences\r\n            if (updatedCookie) {\r\n                prefs.set(BracketsPreferencesModel.IMS_COOKIE, cookie);\r\n            }\r\n\r\n            // Serialize as a single string\r\n            var cookieString = \"\";\r\n\r\n            _.each(cookie, function (val, key) {\r\n                cookieString += key + \"=\" + encodeURIComponent(val) + \"; \";\r\n            });\r\n\r\n            // Update proxy\r\n            IMSProxy.setCookie(cookieString);\r\n        },\r\n\r\n        _initializeCookie: function () {\r\n            var self = this;\r\n            \r\n            // Save cookies when modified\r\n            $adobeid.on(\"cookie\", function (event, cookie) {\r\n                // Merge new cookie with current\r\n                var currentCookie = prefs.get(BracketsPreferencesModel.IMS_COOKIE),\r\n                    newCookie = self._parseCookie(cookie),\r\n                    mergedCookie = _.extend({}, currentCookie, newCookie);\r\n\r\n                // Update proxy server with new cookie\r\n                self._setCookie(mergedCookie);\r\n            });\r\n\r\n            // Restore cookies on init\r\n            self._setCookie(prefs.get(BracketsPreferencesModel.IMS_COOKIE));\r\n        },\r\n        \r\n        isSignedInUser: function (isReAuthentication, client_id, scope) {\r\n            if (imsReady) {\r\n                // Convert to boolean\r\n                return !!imslib.isSignedInUser(isReAuthentication, client_id, scope);\r\n            }\r\n\r\n            // A non-empty cache implies the user was previously signed in\r\n            return !CacheManager.isEmpty();\r\n        },\r\n        \r\n        _doOptionalSignIn: function () {\r\n            if (!this.isSignedInUser()) {\r\n                return this.signIn();\r\n            }\r\n            \r\n            return new $.Deferred().resolve().promise();\r\n        },\r\n        \r\n        getUserProfile: function () {\r\n            return this._doOptionalSignIn().then(function () {\r\n                return imslib && imslib.getUserProfile();\r\n            });\r\n        },\r\n        \r\n        getAccessToken: function () {\r\n            // Only log missing/valid access token when requested\r\n            if (imslib && imslib.getAccessToken()) {\r\n                graphite.events.trigger(\"valid-access-token\");\r\n            } else {\r\n                graphite.events.trigger(\"missing-access-token\");\r\n            }\r\n\r\n            return this._doOptionalSignIn().then(function () {\r\n                return (imslib && imslib.getAccessToken()) || \"\";\r\n            });\r\n        },\r\n\r\n        _showOfflineDialog: function (strAction) {\r\n            // Show dialog if we can\'t load IMS or we\'re offline\r\n            Dialogs.showModalDialog(\r\n                DefaultDialogs.DIALOG_ID_ERROR,\r\n                Strings.EXTENSION_NAME,\r\n                StringUtils.format(Strings.ERROR_OFFLINE_MESSAGE, strAction)\r\n            );\r\n        },\r\n\r\n        _doOptionalLoadIMS: function (actionDeferred, strAction) {\r\n            var self = this,\r\n                deferred = new $.Deferred();\r\n\r\n            var handleNetworkError = function () {\r\n                self._showOfflineDialog(strAction);\r\n            };\r\n\r\n            $(this).one(\"networkError\", handleNetworkError);\r\n\r\n            actionDeferred.always(function () {\r\n                $(self).off(\"networkError\", handleNetworkError);\r\n            });\r\n\r\n            if (imsReady) {\r\n                // May not be currently online, but at least imslib is ready\r\n                deferred.resolve();\r\n            } else {\r\n                loadIMS().then(deferred.resolve, function () {\r\n                    self._showOfflineDialog(strAction);\r\n                    deferred.reject();\r\n                });\r\n            }\r\n\r\n            return deferred.promise();\r\n        },\r\n\r\n        // User can switch between signIn->signUp and back inside SUSI UI. But\r\n        // not from signUp->signIn. When _dialogClosed is fired, we don\'t know\r\n        // whether the user signed in or signed up.\r\n        _signInOrSignUp: function (isSignUp, api_parameters, contextToBePassedOnRedirect) {\r\n            var self = this,\r\n                deferred = new $.Deferred(),\r\n                errString = isSignUp ? Strings.ERROR_OFFLINE_MESSAGE_SIGN_UP : Strings.ERROR_OFFLINE_MESSAGE_SIGN_IN,\r\n                loadIMSPromise = this._doOptionalLoadIMS(deferred, errString),\r\n                promise = deferred.promise();\r\n\r\n            this._updateMetrics(isSignUp, \"attemptCount\");\r\n\r\n            loadIMSPromise.then(function () {\r\n                var dialogStarted = Date.now();\r\n\r\n                // event handlers when popup window closes\r\n                $(self).one(\"_dialogClosed\", function (event) {\r\n                    var eventParams = {\r\n                        state: \"login dialog\",\r\n                        isSignUp: isSignUp,\r\n                        duration: Date.now() - dialogStarted\r\n                    };\r\n\r\n                    // TODO The following login events can only be captured by IMS:\r\n                    // login-attempt, login-failure\r\n                    if (self.isSignedInUser()) {\r\n                        this._updateMetrics(isSignUp, \"successCount\");\r\n                        graphite.events.trigger(\"login-success\", eventParams);\r\n                    } else {\r\n                        this._updateMetrics(isSignUp, \"abortCount\");\r\n                        graphite.events.trigger(\"login-cancel\", eventParams);\r\n                        deferred.reject();\r\n                    }\r\n                });\r\n\r\n                $(self).one(\"signIn\", function (event, data) {\r\n                    deferred.resolve(data);\r\n                });\r\n                \r\n                if (isSignUp) {\r\n                    imslib.signUp(api_parameters, contextToBePassedOnRedirect);\r\n                } else {\r\n                    imslib.signIn(api_parameters, contextToBePassedOnRedirect);\r\n                }\r\n                \r\n                $(self).triggerHandler(isSignUp ? \"signUpDialog\" : \"signInDialog\");\r\n                graphite.events.trigger(\"login-start\", {\r\n                    state: \"login dialog\",\r\n                    isSignUp: isSignUp\r\n                });\r\n            }, deferred.reject);\r\n            \r\n            return promise;\r\n        },\r\n        \r\n        signIn: function (api_parameters, contextToBePassedOnRedirect) {\r\n            return this._signInOrSignUp(false, api_parameters, contextToBePassedOnRedirect);\r\n        },\r\n\r\n        signUp: function (api_parameters, contextToBePassedOnRedirect) {\r\n            return this._signInOrSignUp(true, api_parameters, contextToBePassedOnRedirect);\r\n        },\r\n        \r\n        signOut: function (api_parameters) {\r\n            var self = this,\r\n                deferred = new $.Deferred(),\r\n                loadIMSPromise = this._doOptionalLoadIMS(deferred, Strings.ERROR_OFFLINE_MESSAGE_SIGN_OUT),\r\n                promise = deferred.promise();\r\n            \r\n            loadIMSPromise.then(function () {\r\n                var dialogStarted = Date.now();\r\n\r\n                // event handlers when popup window closes\r\n                $(self).one(\"_dialogClosed\", function (event) {\r\n                    var eventParams = { duration: Date.now() - dialogStarted };\r\n\r\n                    if (!self.isSignedInUser()) {\r\n                        graphite.events.trigger(\"logout-success\", eventParams);\r\n                    } else {\r\n                        graphite.events.trigger(\"logout-failure\", eventParams);\r\n                    }\r\n                });\r\n\r\n                $(self).one(\"signOut\", deferred.resolve);\r\n                \r\n                imslib.signOut(api_parameters);\r\n                graphite.events.trigger(\"logout-attempted\");\r\n            }, deferred.reject);\r\n            \r\n            return promise;\r\n        },\r\n    \r\n        toggleAuth: function () {\r\n            if (this.isSignedInUser()) {\r\n                this.signOut();\r\n            } else {\r\n                this.signIn();\r\n            }\r\n        }\r\n    });\r\n    \r\n    var wrapper = new IMSLibWrapper();\r\n    loadIMS();\r\n    \r\n    // Help > Sign In/Out of Creative Cloud\r\n    cmdToggleAuth = Commands.register(Strings.MENU_CC_SIGN_IN, Commands.TOGGLE_AUTH, function () {\r\n        ExtensionMetaData.getAcceptedEULA().done(function () {\r\n            wrapper.toggleAuth();\r\n        });\r\n    });\r\n    \r\n    // Disable until we receive `ready` event\r\n    cmdToggleAuth.setEnabled(false);\r\n    \r\n    return wrapper;\r\n});\n//# sourceURL=/./ims/IMSLibWrapper.js");

