eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2013 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true */\r\n/*global define: true, graphite: true, console: true, brackets*/\r\n\r\ndefine(\'override/config/environment\',[\r\n    \'jquery\',\r\n    \'module\',\r\n    \'text!config-json\'\r\n], function ($, module, configText) {\r\n    \"use strict\";\r\n    \r\n    var ExtensionUtils = brackets.getModule(\"utils/ExtensionUtils\"),\r\n        configJSON = JSON.parse(configText);\r\n\r\n    var environment = {\r\n        IMS_HOST : configJSON.IMS_HOST,\r\n        LOGOUT_HOST : configJSON.LOGOUT_HOST,\r\n        IMS_CLIENT_ID : configJSON.IMS_CLIENT_ID,\r\n        IMS_TARGET_ENV : configJSON.IMS_TARGET_ENV,\r\n        IMSLIB_URL : configJSON.IMSLIB_URL,\r\n        ADOBE_ANALYTICS_ACCOUNT : configJSON.ADOBE_ANALYTICS_ACCOUNT,\r\n        ENVIRONMENT : \"stage\",\r\n\r\n        ASSETS_HOST : configJSON.ASSETS_HOST,\r\n        IMAGE_SERVICE_HOST : configJSON.IMAGE_SERVICE_HOST,\r\n        IMAGE_SERVICE_PORT: configJSON.IMAGE_SERVICE_PORT,\r\n        FILESTORAGE_HOST: configJSON.FILESTORAGE_HOST,\r\n        COLLAB_SERVICE_HOST: configJSON.COLLAB_SERVICE_HOST,\r\n        COLLAB_IMAGE_SERVICE_HOST: configJSON.COLLAB_IMAGE_SERVICE_HOST,\r\n\r\n        API_KEY: configJSON.API_KEY,\r\n        proxies: {},\r\n\r\n        getImsAuthEndpoint_Login: function () {\r\n            return \"https://\" + environment.IMS_HOST + \"/ims/authorize/v1\";\r\n        },\r\n        getImsAuthEndpoint_Logout: function () {\r\n            return \"https://\" + environment.LOGOUT_HOST + \"/ims/logout/v1\";\r\n        },\r\n        url: function (pathname, service) {\r\n            service = service || environment.IMAGE_SERVICE_HOST;\r\n            \r\n            // TODO fix this:  for some odd reason the createwebasset call fails if it goes through our proxy.\r\n            // Proxy for create web asset disabled for now. However there is no good reason to cache \r\n            // these calls anyways.\r\n            if (environment.proxies[service] && pathname !== \"/createwebasset\" &&\r\n                    !pathname.match(/archive/)) {\r\n                return environment.proxies[service] + pathname;\r\n            } else {\r\n                return service + pathname;\r\n            }\r\n\r\n        },\r\n        setProxyURL: function (proxy, target) {\r\n            environment.proxies[target] = proxy;\r\n        },\r\n        modulePath: function () {\r\n            return ExtensionUtils.getModulePath(module) + \"..\";\r\n        }\r\n    };\r\n\r\n    return environment;\r\n});\r\n\n//# sourceURL=/./config/environment.js");

