eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50, regexp: true  */\r\n/*global define, window, graphite */\r\n\r\ndefine(\'cceco/CollabServiceAPI\',[\r\n    \"jquery\",\r\n    \"config/environment\",\r\n    \"ccweb.files.extract/Constants\"\r\n], function ($, environment, Constants) {\r\n    \"use strict\";\r\n\r\n    var ASSETS_HOST = environment.ASSETS_HOST.replace(/\\./g, \"\\\\.\"), // escape dot\r\n        RE_CREATIVE_LINK = new RegExp(\"^https://\" + ASSETS_HOST + \"/link/(\\\\S+)\"),\r\n        RE_RESOURCE = /^https:\\/\\/[^.]+.adobesc.com\\/api\\/v1\\/assets\\/(.+)$/;\r\n    \r\n    var CollabServiceAPI = {\r\n\r\n        ERROR_INVALID_SHARE_LINK: 1,\r\n        ERROR_LOAD: 2,\r\n\r\n        /**\r\n         * TODO\r\n         * https://wiki.corp.adobe.com/display/parfait/Imaging+Service+Extension#ImagingServiceExtension-ShareLinkAssetHandling\r\n         */\r\n        urlToShareLinkGUID: function (url, successCallback, errorCallback) {\r\n            var aboutBlankLoaded = false;\r\n\r\n            // Validate share link host\r\n            // This is CRITICAL so that we only load trusted adobe.ly URLs\r\n            // so that other, untrusted URLs aren\'t loaded in an iframe.\r\n            if (!url.match(Constants.RE_SHORT_URL)) {\r\n                // Log metric when creative URL input is an untrusted URL\r\n                graphite.events.trigger(\"creative-url-failure\", {\r\n                    reason: \"ERROR_INVALID_SHARE_LINK\"\r\n                });\r\n\r\n                errorCallback(CollabServiceAPI.ERROR_INVALID_SHARE_LINK);\r\n                return;\r\n            }\r\n\r\n            // HACK\r\n            // XMLHttpRequest silently follows redirects from the short URL\r\n            // and there\'s no way to access the final destination. Use an\r\n            // iframe here as a workaround.\r\n            var iframe = window.document.createElement(\"iframe\");\r\n\r\n            function always() {\r\n                window.document.body.removeChild(iframe);\r\n            }\r\n\r\n            function reject() {\r\n                always();\r\n\r\n                // Log metric when creative URL fails to load\r\n                graphite.events.trigger(\"creative-url-failure\", {\r\n                    reason: \"ERROR_LOAD\"\r\n                });\r\n\r\n                errorCallback(CollabServiceAPI.ERROR_LOAD);\r\n            }\r\n\r\n            // Hide the iframe\r\n            iframe.style.display = \"none\";\r\n\r\n            // Setup load and error event handlers\r\n            iframe.onload = function () {\r\n                // Parse redirect URL\r\n                var href = iframe.contentWindow.location.href,\r\n                    location = href === \"about:blank\" ? null : href,\r\n                    exec = location && RE_CREATIVE_LINK.exec(location);\r\n\r\n                if (exec) {\r\n                    // Resolved to expected URL https://assets-stage.adobecc.com/link/GUID\r\n                    always();\r\n\r\n                    // Log metric when creative URL resolves successfully\r\n                    graphite.events.trigger(\"creative-url-success\");\r\n\r\n                    successCallback(exec[1]);\r\n                } else if (href && !location) {\r\n                    // Ignore initial about:blank and wait for next load event\r\n                    aboutBlankLoaded = true;\r\n                } else {\r\n                    // Unexpected URL\r\n                    reject();\r\n                }\r\n            };\r\n            \r\n            iframe.onerror = reject;\r\n\r\n            // Load the short URL\r\n            iframe.src = url;\r\n\r\n            // Expected error on redirect:\r\n            // Refused to display \'<short url>\' in a frame because it set \'X-Frame-Options\' to \'DENY\'. \r\n            try {\r\n                window.document.body.appendChild(iframe);\r\n            } catch (err) {\r\n                // do nothing\r\n            }\r\n        },\r\n        \r\n        /**\r\n         * TODO\r\n         * https://git.corp.adobe.com/Stormcloud/docs/blob/master/features/public_plugins/acl.md\r\n         */\r\n        links: function (shareLinkId, successCallback, errorCallback) {\r\n            var url = environment.url(\"/links/\" + shareLinkId, environment.COLLAB_SERVICE_HOST);\r\n\r\n            $.ajax(url).then(function (data, textStatus, jqXHR) {\r\n                var exec = RE_RESOURCE.exec(data.resource);\r\n                successCallback(data.id);\r\n            }, function (data, textStatus, errorThrown) {\r\n                errorCallback(CollabServiceAPI.ERROR_LOAD);\r\n            });\r\n        }\r\n    };\r\n\r\n    return CollabServiceAPI;\r\n});\n//# sourceURL=/lib/graphite-api/public/js/cceco/CollabServiceAPI.js");

