eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\r\n/*global define, brackets, graphite*/\r\n\r\ndefine(\'ext/models/CSSHintModel\',[\r\n    \"jquery\",\r\n    \"backbone\",\r\n    \"ext/Constants\",\r\n    \"serverAPI\",\r\n    \"ccweb.files.extract/Constants\",\r\n    \"ccweb.files.extract/controllers/SelectionController\",\r\n    \"ext/utils/HintUtil\",\r\n    \"ext/strings\"\r\n], function ($, Backbone, Constants, serverAPI, ParfaitConstants, SelectionController, HintUtil, Strings) {\r\n    \"use strict\";\r\n\r\n    var CSSUtils        = brackets.getModule(\"language/CSSUtils\"),\r\n        EditorManager   = brackets.getModule(\"editor/EditorManager\"),\r\n        HTMLUtils       = brackets.getModule(\"language/HTMLUtils\");\r\n\r\n    var FILE_EXT_TO_FORMAT_MAP = {\r\n        \"png\": \"png32\",\r\n        \"png8\": \"png8\",\r\n        \"png32\": \"png32\",\r\n        \"svg\": \"svg\",\r\n        \"jpg\": \"jpeg\",\r\n        \"jpeg\": \"jpeg\"\r\n    };\r\n\r\n    var FILE_EXT_TO_CANVAS_MAP = {\r\n        \"png\": \"png\",\r\n        \"jpg\": \"jpeg\",\r\n        \"jpeg\": \"jpeg\",\r\n        \"webp\": \"webp\"\r\n    };\r\n    \r\n    var CSSHintModel = Backbone.Model.extend({\r\n        \r\n        defaults: {\r\n            providerType: \"css\",\r\n            layerModel: null,\r\n            displayName: null,\r\n            displayDescription: null,\r\n            property: null,\r\n            value: \"\",\r\n            displayValue: \"\",\r\n            type: Constants.Hint.OTHER\r\n        },\r\n\r\n        initialize: function () {\r\n            var property = this.get(\"property\"),\r\n                displayName = this.get(\"displayName\"),\r\n                displayDescription = this.get(\"displayDescription\"),\r\n                isMergeLayers = this.isMergeLayers();\r\n\r\n            // property is not set for bulk hints\r\n            if (!property && displayName && displayDescription) {\r\n                this.set(\"property\", displayName + \" \" + displayDescription);\r\n            }\r\n\r\n            this.set(\"isMergeLayers\", isMergeLayers);\r\n        },\r\n        \r\n        isBulkHint: function () {\r\n            var value = this.get(\"value\");\r\n            return Array.isArray(value) && value.length > 1;\r\n        },\r\n\r\n        toHint: function (context) {\r\n            var property = this.get(\"property\"),\r\n                value = this.get(\"value\"),\r\n                hint;\r\n\r\n            if (Array.isArray(value)) {\r\n                var hints = [];\r\n\r\n                value.forEach(function (cssData) {\r\n                    hints.push(cssData.property + \": \" + cssData.value + \";\");\r\n                });\r\n\r\n                hint = hints.join(\"\\n\");\r\n            } else if (context === CSSUtils.PROP_NAME) {\r\n                hint =  property + \": \" + value + \";\";\r\n            } else if (context === HTMLUtils.ATTR_NAME) {\r\n                hint =  property + \"=\\\"\" + value + \"\\\"\";\r\n            } else {\r\n                hint = value;\r\n            }\r\n\r\n            return hint;\r\n        },\r\n\r\n        getLayers: function () {\r\n            var layerModel = this.get(\"layerModel\");\r\n            \r\n            return this.isMergeLayers() ? layerModel : [layerModel];\r\n        },\r\n\r\n        isMergeLayers: function () {\r\n            return Array.isArray(this.get(\"layerModel\"));\r\n        },\r\n\r\n        isExportableAsSVG: function () {\r\n            var layers = this.getLayers();\r\n            \r\n            return layers.every(function (layerModel) {\r\n                if ((layerModel.get(\"type\") !== \"contentLayer\") && (layerModel.get(\"type\") !== \"textLayer\")) {\r\n                    return false;\r\n                }\r\n\r\n                return true;\r\n            });\r\n        },\r\n        \r\n        toDataURL: function (requestedType) {\r\n            var self = this,\r\n                type = requestedType.toLowerCase(),\r\n                isMergeLayers = this.isMergeLayers(),\r\n                layers = this.getLayers(),\r\n                layerType = isMergeLayers ? ParfaitConstants.Type.LAYER_GROUP : layers[0].attributes.properties.layerType,\r\n                isGroup = layerType === ParfaitConstants.Type.LAYER_GROUP,\r\n                format = FILE_EXT_TO_FORMAT_MAP[type],\r\n                canvasFormat = FILE_EXT_TO_CANVAS_MAP[type],\r\n                psdModel = SelectionController.getPSDModel(),\r\n                isLocalContent = !!psdModel.get(\"localContent\"),\r\n                deferred = new $.Deferred(),\r\n                visibleLayers = isGroup ? HintUtil.getVisibleLayers(layers) : layers,\r\n                numLayers = visibleLayers.length,\r\n                editor = EditorManager.getActiveEditor();\r\n\r\n            function toCanvasFallback() {\r\n                deferred.resolve(self.get(\"canvas\").toDataURL(\"image/\" + canvasFormat));\r\n            }\r\n            \r\n            if (!numLayers) {\r\n                // If no visible layers are selected, fallback to all layers\r\n                deferred.reject(Strings.NO_VISIBLE_LAYERS_ERROR);\r\n            } else if (type === \"svg\" && (isLocalContent || !this.isExportableAsSVG())) {\r\n                var svgError = isLocalContent ? Strings.STATIC_CONTENT_SVG_NOT_AVAILABLE : Strings.SVG_NOT_AVAILABLE;\r\n\r\n                // Error handling if SVG can\'t be derived\r\n                deferred.reject(svgError);\r\n            } else if (format && !isLocalContent) {\r\n                graphite.events.trigger(\"extract-asset-downloading\", {\r\n                    format: format,\r\n                    numLayers: numLayers\r\n                });\r\n\r\n                // Attempt service call to extract asset\r\n                serverAPI.getDerivedAsset(psdModel, visibleLayers, format)\r\n                    .done(function (result) {\r\n                        graphite.events.trigger(\"extract-asset-downloaded\", {\r\n                            format: format,\r\n                            numLayers: numLayers\r\n                        });\r\n\r\n                        deferred.resolve(result);\r\n                    })\r\n                    .fail(function (err) {\r\n                        var errString = isLocalContent ? Strings.STATIC_CONTENT_SERVICE_ERROR : Strings.SERVICE_ERROR;\r\n\r\n                        graphite.events.trigger(\"extract-asset-error\", {\r\n                            format: format,\r\n                            numLayers: numLayers,\r\n                            err: err,\r\n                            errString: errString\r\n                        });\r\n\r\n                        // Service call failed\r\n                        if (canvasFormat && !isGroup) {\r\n                            graphite.events.trigger(\"extract-asset-canvas-fallback\", {\r\n                                format: format,\r\n                                numLayers: numLayers\r\n                            });\r\n\r\n                            // fallback to canvas for single-layers (non-groups)\r\n                            toCanvasFallback();\r\n                        } else {\r\n                            // reject with service call error\r\n                            deferred.reject(errString, err);\r\n                        }\r\n                    });\r\n            } else if (!isGroup && canvasFormat && isLocalContent) {\r\n                graphite.events.trigger(\"extract-asset-canvas\", {\r\n                    format: format,\r\n                    numLayers: numLayers\r\n                });\r\n\r\n                // Enable image extraction via canvas for local content, i.e.\r\n                // 1st launch help content.\r\n                toCanvasFallback();\r\n            } else {\r\n                var errString;\r\n                \r\n                if (isGroup && isLocalContent) {\r\n                    // Show useful error in case not logged in users try to extract\r\n                    // images from group layers while using 1st launch help content.\r\n                    errString = Strings.STATIC_CONTENT_SERVICE_ERROR;\r\n                } else {\r\n                    // Unsupported file format\r\n                    errString = Strings.FILE_FORMAT_ERROR;\r\n                }\r\n                \r\n                graphite.events.trigger(\"extracted-asset-error\", {\r\n                    format: type,\r\n                    numLayers: numLayers,\r\n                    errString: errString\r\n                });\r\n\r\n                deferred.reject(errString, type);\r\n            }\r\n            \r\n            return deferred.promise();\r\n        }\r\n        \r\n    });\r\n    \r\n    return CSSHintModel;\r\n});\n//# sourceURL=/./models/CSSHintModel.js");

