eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\r\n/*global define, graphite, brackets, decodeURI*/\r\n\r\ndefine(\'ext/CacheManager\',[\r\n    \"jquery\",\r\n    \"underscore\",\r\n    \"ext/NodeAPI\",\r\n    \"ext/models/BracketsPreferencesModel\",\r\n    \"ext/strings\"\r\n], function ($, _, NodeAPI, BracketsPreferencesModel, Strings) {\r\n    \"use strict\";\r\n    \r\n    var nodeDomain  = NodeAPI.nodeDomain,\r\n        prefs       = BracketsPreferencesModel.get(\"preferenceSystem\");\r\n\r\n    var CacheManager = {\r\n\r\n        currentMD5s: {},\r\n    \r\n        purgeCache: function (key) {\r\n            var cacheMap = this.getCache();\r\n            \r\n            if (key) {\r\n                delete cacheMap[key];\r\n                nodeDomain.exec(\"clearProxyCache\", decodeURI(key));\r\n            } else {\r\n                cacheMap = {};\r\n                nodeDomain.exec(\"clearProxyCache\", null);\r\n            }\r\n            \r\n            this.setCache(cacheMap);\r\n        },\r\n\r\n        isEmpty: function () {\r\n            return Object.keys(this.getCache()).length === 0;\r\n        },\r\n        \r\n        getCache: function () {\r\n            return prefs.get(BracketsPreferencesModel.CCFILE_CACHE);\r\n        },\r\n        \r\n        setCache: function (cacheMap) {\r\n            prefs.set(BracketsPreferencesModel.CCFILE_CACHE, cacheMap);\r\n        },\r\n        \r\n        getGraphiteVersion: function () {\r\n            return prefs.get(BracketsPreferencesModel.GRAPHITE_VERSION);\r\n        },\r\n        \r\n        setGraphiteVersion: function (graphiteVersion) {\r\n            prefs.set(BracketsPreferencesModel.GRAPHITE_VERSION, graphiteVersion);\r\n        },\r\n        \r\n        validateCacheForPath: function (md5, path) {\r\n            this.validateKey(md5, path);\r\n        },\r\n        \r\n        validateKey: function (value, key) {\r\n            var cacheMap = this.getCache();\r\n            \r\n            if (cacheMap[key] && cacheMap[key] !== value) {\r\n                // There is a new or updated PSD. Purge cache.\r\n                this.purgeCache(key);\r\n            }\r\n        },\r\n\r\n        handleCCDirLoad: function (params) {\r\n            var self = this,\r\n                cacheMap = this.getCache(),\r\n                loadedMD5s = params.pathMD5Map,\r\n                currentPath = params.path;\r\n\r\n            this.currentMD5s = loadedMD5s;\r\n\r\n            _.each(this.currentMD5s, function (value, key) {\r\n                self.validateKey(value, key);\r\n            });\r\n\r\n            // Purge cache for paths that have been deleted from CC\r\n            _.each(cacheMap, function (value, key) {\r\n                var thisKeysFolder = key.substr(0, key.lastIndexOf(\"/\"));\r\n\r\n                if (thisKeysFolder === currentPath &&\r\n                        !self.currentMD5s[key]) {\r\n                    // A PSD has been deleted from this folder, purge cache\r\n                    self.purgeCache(key);\r\n                }\r\n            });\r\n        },\r\n\r\n        handleAssetDataLoaded: function (params) {\r\n            var cacheMap = this.getCache(),\r\n                graphiteVersion = params.graphiteVersion,\r\n                path = params.path,\r\n                cachedGraphiteVersion = this.getGraphiteVersion();\r\n\r\n            // FIXME ccweb/main.js do not cache authless PSDs\r\n            if (path.indexOf(\"files/\") < 0) {\r\n                return;\r\n            }\r\n\r\n            if (graphiteVersion && cachedGraphiteVersion && graphiteVersion !== cachedGraphiteVersion) {\r\n                this.purgeCache();\r\n            } else {\r\n                // WHen a PSD is opened we assume that currentMD5s holds\r\n                // the current MD5 for the PSD in view. \r\n                // If we ever implement a way to open a PSD without first browsing to the folder\r\n                // we will have to ensure currentMD5s is updated before this handler runs.\r\n                cacheMap[path] = this.currentMD5s[path];\r\n                this.setCache(cacheMap);\r\n            }\r\n\r\n            this.setGraphiteVersion(graphiteVersion);\r\n        }\r\n    };\r\n\r\n    return CacheManager;\r\n});\r\n\n//# sourceURL=/./CacheManager.js");

