eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\r\n/*global define*/\r\n\r\ndefine(\'override/BracketsServerAPI\',[\r\n    \"serverAPI\",\r\n    \"ext/ims/IMSLibWrapper\",\r\n    \"ext/metrics/MixpanelWrapper\",\r\n    \"ext/metrics/AnalyticsWrapper\"\r\n], function (serverAPI, IMSLibWrapper, MixpanelWrapper, AnalyticsWrapper) {\r\n    \"use strict\";\r\n    \r\n    var metricsServices = [MixpanelWrapper, AnalyticsWrapper];\r\n    \r\n    serverAPI.flushMetrics = function (flushAsync) {\r\n        metricsServices.forEach(function (service) {\r\n            service.flush(flushAsync);\r\n        });\r\n    };\r\n\r\n    serverAPI.trackEvent = function (params, successCallback, errorCallback, context) {\r\n        //console.log(\"trackEvent: \", params.eventname, params.subevent, params.timestamp);\r\n        \r\n        metricsServices.forEach(function (service) {\r\n            try {\r\n                service.track(params.eventname, params);\r\n            } catch (err) {\r\n                console.error(err);\r\n            }\r\n        });\r\n    };\r\n    \r\n    serverAPI.customHeader = {};\r\n\r\n    var getAssetFromUrl = serverAPI.getAssetFromUrl.bind(serverAPI);\r\n    serverAPI.getAssetFromUrl = function (url, callback, model) {\r\n        if (model) {\r\n            serverAPI.customHeader = {\'e4b-cache-path\': model.get(\"path\")};\r\n        }\r\n\r\n        return getAssetFromUrl(url, callback);\r\n    };\r\n\r\n    // https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/628\r\n    // White-list of methods that require authorization. vanilla_extract\r\n    // doesn\'t account for async auth yet. The methods here must be async and\r\n    // either expect these methods to return a promise or pass a callback.\r\n    var AUTHORIZED_CALLS = [\r\n        \"loadAssetMetadata\",\r\n        \"loadAssetCollection\",\r\n        /*\"loadPsdModelFromPath\",*/ // ImageServiceAPIs introspect and spritesheets can be authless\r\n        \"getAssetFromUrl\",\r\n        /*\"getDerivedAsset\",*/ // ImageServiceAPI.createWebAsset can be authless\r\n        \"deleteAsset\",\r\n        \"loadDerivedAssetCollection\",\r\n        /*\"getRendition\",*/ // ImageServiceAPI.createWebAsset can be authless\r\n        /*\"getDerivedSpriteImage\",*/ // synchronous return of an Image node\r\n        \"upload\"\r\n    ];\r\n\r\n    AUTHORIZED_CALLS.forEach(function (methodName) {\r\n        var fn = serverAPI[methodName];\r\n\r\n        serverAPI[methodName] = function () {\r\n            var args = Array.prototype.slice.call(arguments, 0);\r\n            return IMSLibWrapper.getAccessToken().then(function () {\r\n                return fn.apply(serverAPI, args);\r\n            });\r\n        };\r\n    });\r\n\r\n    return serverAPI;\r\n\r\n});\r\n\n//# sourceURL=/./BracketsServerAPI.js");

