                 // Add non-group layers only\r\n                        visibleLayers.push(currentLayer);\r\n                    }\r\n                }\r\n            }\r\n\r\n            if (anyVisibleLayer) {\r\n                return false;\r\n            }\r\n            \r\n            return visibleLayers;\r\n        },\r\n\r\n        hasVisibleLayers: function (layers) {\r\n            return this._getVisibleLayers(layers, true);\r\n        },\r\n    \r\n        getVisibleLayers: function (layers) {\r\n            return this._getVisibleLayers(layers);\r\n        }\r\n\r\n    };\r\n\r\n    return HintUtil;\r\n});\r\n\n//# sourceURL=/./utils/HintUtil.js");

