eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2013 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, regexp: true */\r\n/*global define: true, graphite: true, brackets, Image*/\r\n\r\ndefine(\'override/routes/BracketsAppRouter\',[\r\n    \'jquery\',\r\n    \'underscore\',\r\n    \'backbone\',\r\n    \'models/AuthModel\',\r\n    \'views/landing/AssetListView\',\r\n    \'ccweb.files.extract/views/detail/PSDDetailsView\',\r\n    \'controllers/AuthController\',\r\n    \'ext/Constants\',\r\n    \'ext/CacheManager\',\r\n    \'ext/models/BracketsPreferencesModel\',\r\n    \'ccweb.files.extract/main\',\r\n    \'cceco/CollabServiceAPI\'\r\n], function ($, _, Backbone, AuthModel, AssetListView, BracketsPSDDetailsView,\r\n    AuthController, PSDLensConstants, CacheManager, BracketsPreferencesModel,\r\n    ExtractPlugin, CollabServiceAPI) {\r\n    \"use strict\";\r\n\r\n    var FileSystem        = brackets.getModule(\"filesystem/FileSystem\");\r\n\r\n    var prefs = BracketsPreferencesModel.get(\"preferenceSystem\");\r\n    \r\n    function _getStaticContentFolder(basepath) {\r\n        var deferred    = new $.Deferred(),\r\n            lang        = brackets.getLocale(),\r\n            shortLang   = lang.split(\"-\")[0],\r\n            locpath     = basepath,\r\n            folder;\r\n        \r\n        if (shortLang !== \"en\") {\r\n            locpath += \'/\' + shortLang + \'/\';\r\n            folder = FileSystem.getDirectoryForPath(locpath);\r\n            folder.exists(function (err, exists) {\r\n                if (!err && exists) {\r\n                    // localized content does exist, so return the new path\r\n                    deferred.resolve(locpath);\r\n                } else {\r\n                    // localized content does not exist, so return the original path\r\n                    deferred.resolve(basepath);\r\n                }\r\n            });\r\n            \r\n            return deferred.promise();\r\n        }\r\n        \r\n        // English content lives at the root path always\r\n        return deferred.resolve(basepath);\r\n    }\r\n        \r\n    var ApplicationRouter = Backbone.Router.extend({\r\n        authModel : null,\r\n        routes: {\r\n            \"asset/psd/:id/*route\": PSDLensConstants.Routes.ASSET,\r\n            \"authless/:url\": PSDLensConstants.Routes.AUTHLESS_ASSET,\r\n            \"first_launch\": PSDLensConstants.Routes.FIRST_LAUNCH,\r\n            \"hide\": PSDLensConstants.Routes.HIDE,\r\n            \"files/*route\": PSDLensConstants.Routes.FOLDER\r\n        },\r\n\r\n        initialize: function () {\r\n            this.authModel = new AuthModel();\r\n            AuthController.setAuthModel(this.authModel);\r\n\r\n            // Listen for re-auth requests\r\n            graphite.events.on(\"reauthenticate\", this.handleAuthorize, this);\r\n            graphite.events.on(\"load-login-page\", this.handleLoadLoginPage, this);\r\n            \r\n            // Listen for first route-change\r\n            graphite.events.once(\"route-change\", function () {\r\n                graphite.events.trigger(\"launch\");\r\n            });\r\n\r\n            // Convert \"route\" events on the router to \"route-change\" events\r\n            this.on(\"route\", function (page) {\r\n                graphite.events.trigger(\'route-change\', page);\r\n\r\n                // Set persistent param only for routes that render a PSD\r\n                var authless,\r\n                    psdCountProperty;\r\n\r\n                if (page === PSDLensConstants.Routes.AUTHLESS_ASSET) {\r\n                    authless = true;\r\n                    psdCountProperty = \"psdViews.creativeUrlCount\";\r\n                } else if (page === PSDLensConstants.Routes.ASSET) {\r\n                    authless = false;\r\n                    psdCountProperty = \"psdViews.userCount\";\r\n                } else if (page === PSDLensConstants.Routes.FIRST_LAUNCH) {\r\n                    psdCountProperty = \"psdViews.gettingStartedCount\";\r\n                }\r\n\r\n                graphite.metricsProxyModel.setDefaultParam(\"authless\", authless);\r\n\r\n                // Update PSD view count\r\n                if (psdCountProperty) {\r\n                    var defaultParams = prefs.get(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS),\r\n                        count = defaultParams[psdCountProperty] + 1;\r\n\r\n                    defaultParams[psdCountProperty] = count;\r\n\r\n                    // Save prefs\r\n                    prefs.set(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS, defaultParams);\r\n                }\r\n            });\r\n        },\r\n\r\n        handleAuthorize: function () {\r\n            AuthController.reauthenticate();\r\n        },\r\n\r\n        handleLoadLoginPage: function () {\r\n            this.authModel.configure();\r\n        },\r\n\r\n        hidePanel: function () {\r\n            this.destroyViews([\"extractPlugin\", \"assetListView\"]);\r\n        },\r\n        \r\n        firstLaunch: function () {\r\n            var config = {\r\n                segment     : \'psd-lens-first\',  // We use this to segment our metrics. Each one should be unique\r\n                directory   : PSDLensConstants.STATIC_CONTENT_LOCATION + \'marketing/marketing_psd\',\r\n                guid        : \'marketing-psd\'\r\n            };\r\n            \r\n            var _show = function (path) {\r\n                this.destroyViews([\"assetListView\"]);\r\n                config.directory = path;\r\n                this.showExtractPlugin(path, null, config, \"\");\r\n            }.bind(this);\r\n\r\n            _getStaticContentFolder(config.directory).then(_show);\r\n        },\r\n\r\n        showExtractPlugin: function (assetId, layerCompId, localContent, path) {\r\n            this.authModel = AuthController.getAuthModel();\r\n            if (this.extractPlugin) {\r\n                this.extractPlugin.hide();\r\n            }\r\n\r\n            var self = this,\r\n                deferred = new $.Deferred(),\r\n                promise = deferred.promise();\r\n\r\n            if (localContent) {\r\n                // No metadata for first launch content\r\n                deferred.resolve({});\r\n            } else if (assetId) {\r\n                // Get metadata from storage service\r\n                graphite.serverAPI.loadAssetMetadata(path).then(function (data) {\r\n                    CacheManager.validateCacheForPath(data.md5, path);\r\n                    deferred.resolve(data);\r\n                }, deferred.reject);\r\n            } else {\r\n                // If assetID is not passed we are in an authless environment\r\n                // Use CollabServiceAPI to validate URL\r\n                CollabServiceAPI.urlToShareLinkGUID(path, function (shareLinkGuid) {\r\n                    // Create a model with required properties\r\n                    var renditionData = {\r\n                        id: shareLinkGuid,\r\n                        shareLinkGuid: shareLinkGuid,\r\n                        authless: true,\r\n                        path: path\r\n                    };\r\n\r\n                    var renditionPSDModel = new Backbone.Model(renditionData);\r\n\r\n                    // Use ImageServiceAPI to get bounds\r\n                    graphite.serverAPI.getRendition(renditionPSDModel, null, function (err, dataURI) {\r\n                        if (err) {\r\n                            graphite.events.trigger(\"image-service-error\", encodeURI(path), false);\r\n                            deferred.reject(err);\r\n                        } else if (dataURI) {\r\n                            var img = new Image();\r\n\r\n                            img.src = dataURI;\r\n                            renditionData.width = img.width;\r\n                            renditionData.height = img.height;\r\n                            deferred.resolve(renditionData);\r\n                        }\r\n\r\n                        \r\n                    });\r\n                }, function () {\r\n                    deferred.reject();\r\n                });\r\n            }\r\n\r\n            promise.done(function (metadata) {\r\n                // create a shim that looks like ccweb\'s asset_model.js with an id attribute.\r\n                var modelData = {\r\n                    id: \"/\" + assetId,\r\n                    assetId: assetId,\r\n                    localContent: localContent,\r\n                    layerCompId: layerCompId,\r\n                    path: path,\r\n                    name: path.replace(/^.*(\\/)/, \'\'),\r\n                    size: metadata.size,\r\n                    width: metadata.width,\r\n                    height: metadata.height,\r\n                    shareLinkGuid: metadata.shareLinkGuid\r\n                };\r\n                \r\n                modelData.assetId = modelData.assetId || metadata.shareLinkGuid;\r\n                modelData.id = \"/\" + modelData.assetId;\r\n                \r\n                var asset_model = new Backbone.Model(_.extend(metadata, modelData));\r\n                self.extractPlugin = new ExtractPlugin(asset_model);\r\n                var $results = $(\'#results\');\r\n                $results.html(self.extractPlugin.$el);\r\n                self.extractPlugin.show();\r\n                self.extractPlugin.trigger(\'added-to-dom\');\r\n            });\r\n        },\r\n\r\n        handleAsset: function (assetId, path) {\r\n            var self = this;\r\n\r\n            // Validate access token before viewing a file\r\n            AuthController.getAuthModel().getAccessToken().then(function () {\r\n                self.destroyViews([\"assetListView\"]);\r\n                self.showExtractPlugin(assetId, null, false, path);\r\n            });\r\n        },\r\n        \r\n        handleAuthlessAsset: function (url) {\r\n            this.destroyViews([\"assetListView\"]);\r\n            this.showExtractPlugin(null, null, false, url);\r\n        },\r\n\r\n        handleFolder: function (path) {\r\n            var self = this;\r\n\r\n            // Validate access token before viewing a folder\r\n            AuthController.getAuthModel().getAccessToken().then(function () {\r\n                self.destroyViews([\"extractPlugin\", \"assetListView\"]);\r\n                $(\"body\").removeClass(\"not-logged-in\");\r\n                self.assetListView = new AssetListView(AuthController.getAuthModel(), path);\r\n            });\r\n        },\r\n\r\n        destroyViews: function (views) {\r\n            var self = this;\r\n            _.each(views, function (view) {\r\n                if (self.hasOwnProperty(view)) {\r\n                    if (self[view].remove) {\r\n                        self[view].remove();\r\n                    } else if (self[view].hide) {\r\n                        // in case the view is extractPlugin\r\n                        // we have to call hide instead of remove.\r\n                        // TODO: merge change upstream to add function named remove\r\n                        // that calls hide.\r\n                        self[view].hide();\r\n                    }\r\n                    delete self[view];\r\n                }\r\n            });\r\n        }\r\n\r\n    });\r\n\r\n    return ApplicationRouter;\r\n});\r\n\n//# sourceURL=/./routes/BracketsAppRouter.js");

