eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50 */\r\n/*global define, brackets, window, graphite, PathUtils*/\r\n\r\ndefine(\'main\',[\r\n    \"module\",\r\n    \"jquery\",\r\n    \"underscore\",\r\n    \"backbone\",\r\n    \"modernizr\",\r\n    \"mixpanel\",\r\n    \"ccweb.files.extract/main\", /*graphite global init*/\r\n    \"ccweb.files.extract/Constants\",\r\n    \"routes/appRouter\",\r\n    \"ext/command/Commands\",\r\n    \"ext/command/CommandHandlers\", /* init, don\'t remove seemingly unused CommandHandlers */\r\n    \"ext/ParfaitPopup\",\r\n    \"ext/MainPanel\",\r\n    \"ext/lib/linkedmode/linkedmode\",\r\n    \"ext/models/BracketsPreferencesModel\",\r\n    \"ext/models/ExtensionMetadata\",\r\n    \"ext/CacheManager\",\r\n    \"ext/strings\"\r\n], function (module, $, _, Backbone, Modernizr, mixpanel, ExtractPlugin,\r\n        ParfaitConstants, AppRouter, Commands, CommandHandlers, ParfaitPopup,\r\n        MainPanel, linkedmode, BracketsPreferencesModel, ExtensionMetadata,\r\n        CacheManager, Strings) {\r\n    \"use strict\";\r\n\r\n    // From Parfait app.js\r\n    window.graphite.routers = window.graphite.routers || {};\r\n    window.graphite.routers.main = new AppRouter();\r\n    Backbone.history.start();\r\n\r\n    // Restore global Underscore\r\n    _.noConflict();\r\n\r\n    // Brackets dependencies\r\n    var AppInit             = brackets.getModule(\"utils/AppInit\"),\r\n        Async               = brackets.getModule(\"utils/Async\"),\r\n        Dialogs             = brackets.getModule(\"widgets/Dialogs\"),\r\n        DefaultDialogs      = brackets.getModule(\"widgets/DefaultDialogs\"),\r\n        EditorManager       = brackets.getModule(\"editor/EditorManager\"),\r\n        ExtensionManager    = brackets.getModule(\"extensibility/ExtensionManager\"),\r\n        ExtensionUtils      = brackets.getModule(\"utils/ExtensionUtils\"),\r\n        ProjectManager      = brackets.getModule(\"project/ProjectManager\"),\r\n        StringUtils         = brackets.getModule(\"utils/StringUtils\");\r\n    \r\n    // Log certain project open/close metrics only when active:\r\n    // - TOGGLE_PARFAIT_ID command triggered\r\n    // - Extension install\r\n    var _isExtensionActive;\r\n\r\n    var parfaitStyleSheets = [\r\n        \"styles/topcoat_base.css\",\r\n        \"lib/graphite-api/public/css/application.css\",\r\n        \"styles/ccfiles.css\"\r\n    ];\r\n\r\n    var extensionStyleSheets = [\r\n        \"lib/linkedmode/linkedmode.css\",\r\n        \"styles/parfait-panel.less\",\r\n        \"styles/parfait-popup.less\",\r\n        \"styles/parfait-code-hints.less\",\r\n        \"styles/parfait-override.less\"\r\n    ];\r\n\r\n    var _loadStyleSheet = function (path) {\r\n        return ExtensionUtils.loadStyleSheet(module, path);\r\n    };\r\n    \r\n    // Load parfait stylesheets first\r\n    var stylePromise = Async.doInParallel(parfaitStyleSheets, _loadStyleSheet);\r\n\r\n    // Load extension style sheets last\r\n    stylePromise = stylePromise.then(function () {\r\n        return Async.doInParallel(extensionStyleSheets, _loadStyleSheet);\r\n    });\r\n    \r\n    \r\n    function showRenderError(path) {\r\n        var dialog = Dialogs.showModalDialog(\r\n                DefaultDialogs.DIALOG_ID_ERROR,\r\n                Strings.ERROR_RENDER_TITLE,\r\n                StringUtils.format(\r\n                    Strings.ERROR_RENDER_MESSAGE,\r\n                    StringUtils.breakableUrl(decodeURI(path))\r\n                )\r\n            );\r\n\r\n        dialog.getPromise().done(function (id) {\r\n            var route = $(\"#browse-files\")[0].attributes.getNamedItem(\"data-route\").value;\r\n                            \r\n            Backbone.history.navigate(route, true);\r\n        });\r\n    }\r\n    \r\n    function _attachExtensionManagerListener() {\r\n        var hasAppCloseListener = false,\r\n            extensionID = ExtensionMetadata.get(\"id\"),\r\n            markedForUpdate = false;\r\n        \r\n        // Listen for statusChange event for this extension\r\n        $(ExtensionManager).on(\"statusChange\", function (event, id) {\r\n            if (id !== extensionID) {\r\n                return;\r\n            }\r\n            \r\n            markedForUpdate = ExtensionManager.isMarkedForUpdate(extensionID);\r\n            \r\n            if (hasAppCloseListener) {\r\n                return;\r\n            }\r\n            \r\n            // Wait until APP_RELOAD to verify extension status\r\n            $(ProjectManager).on(\"beforeAppClose\", function () {\r\n                var installData = ExtensionManager.extensions[extensionID],\r\n                    wasRemoved = !installData.installInfo;\r\n\r\n                if (wasRemoved) {\r\n                    graphite.events.trigger(\"uninstall-complete\");\r\n                } else if (markedForUpdate) {\r\n                    graphite.events.trigger(\"update-complete\");\r\n                }\r\n            });\r\n            \r\n            hasAppCloseListener = true;\r\n        });\r\n    }\r\n    \r\n    function _attachListeners() {\r\n        var handleCCDirLoad = CacheManager.handleCCDirLoad.bind(CacheManager),\r\n            handleAssetDataLoaded = CacheManager.handleAssetDataLoaded.bind(CacheManager),\r\n            purgeCache = CacheManager.purgeCache.bind(CacheManager),\r\n            offlineAcknowledged = false,\r\n            offlineStart;\r\n        \r\n        graphite.events.on(\"cc-directory-load\", handleCCDirLoad);\r\n        graphite.events.on(\"asset-data-loaded\", handleAssetDataLoaded);\r\n        graphite.events.on(\"image-service-error\", function (path) {\r\n            purgeCache(path);\r\n            showRenderError(path);\r\n        });\r\n        \r\n        // Track online/offline state\r\n        graphite.events.on(\"proxy-request-complete\", function (params) {\r\n            // Ignore if we never went offline\r\n            if (!offlineAcknowledged) {\r\n                return;\r\n            }\r\n            \r\n            // Reset offline ack\r\n            offlineAcknowledged = false;\r\n            graphite.metricsProxyModel.setDefaultParam(\"isOffline\", false);\r\n\r\n            // Log the first successful service call when returning online\r\n            graphite.events.trigger(\"offline-end\", {\r\n                duration: Date.now() - offlineStart\r\n            });\r\n        });\r\n\r\n        // Each service call should handle errors explicitly\r\n        graphite.events.on(\"proxy-request-error\", function (params) {\r\n            // Log all service call errors\r\n            graphite.events.trigger(\"proxy-request-error\");\r\n        });\r\n\r\n        // Notify user when retrieving services from cache\r\n        graphite.events.on(\"cache-fallback-complete\", function (params) {\r\n            if (offlineAcknowledged) {\r\n                return;\r\n            }\r\n\r\n            // Tell the user that we can use the cache\r\n            Dialogs.showModalDialog(\r\n                DefaultDialogs.DIALOG_ID_ERROR,\r\n                Strings.EXTENSION_NAME,\r\n                StringUtils.format(Strings.ERROR_OFFLINE_USE_CACHE_MESSAGE)\r\n            );\r\n\r\n            // Set flag so that we only show the cache dialog once\r\n            offlineStart = Date.now();\r\n            offlineAcknowledged = true;\r\n            graphite.metricsProxyModel.setDefaultParam(\"isOffline\", true);\r\n            \r\n            // Log the first service call that hit the cache\r\n            graphite.events.trigger(\"offline-start\");\r\n        });\r\n    }\r\n\r\n    function _logEditorDocumentType(editor) {\r\n        var language = editor && editor.document.getLanguage(),\r\n            languageName = \"no-active-editor\";\r\n\r\n        if (language && language.getName) {\r\n            languageName = language.getName();\r\n        }\r\n\r\n        // Save active editor language with all events\r\n        graphite.metricsProxyModel.setDefaultParam(\"language\", languageName);\r\n    }\r\n\r\n    function _logProjectOpen(root) {\r\n        var isWelcomeProject = ProjectManager.isWelcomeProjectPath(root.fullPath);\r\n\r\n        // Save isWelcomeProject with all events\r\n        graphite.metricsProxyModel.setDefaultParam(\"isWelcomeProject\", isWelcomeProject);\r\n\r\n        if (_isExtensionActive) {\r\n            graphite.events.trigger(\"project-open\");\r\n        }\r\n    }\r\n    \r\n    function _saveDefaultParams() {\r\n        _logEditorDocumentType(EditorManager.getActiveEditor());\r\n        _logProjectOpen(ProjectManager.getProjectRoot());\r\n    }\r\n\r\n    function _setupAbandonmentEvents() {\r\n        $(ProjectManager).on(\"beforeAppClose\", function () {\r\n            if (_isExtensionActive) {\r\n                graphite.events.trigger(\"app-close\");\r\n            }\r\n        }).on(\"projectClose\", function () {\r\n            if (_isExtensionActive) {\r\n                graphite.events.trigger(\"project-close\");\r\n            }\r\n        }).on(\"projectOpen\", function (event, projectRoot) {\r\n            _logProjectOpen(projectRoot);\r\n        });\r\n    }\r\n\r\n    function _appReadyHandler() {\r\n        // Use runtime metadata from ExtensionManager for version info\r\n        // instead of build-time metadata that does not contain extra build\r\n        // info (e.g. the git SHA)\r\n        var id = ExtensionMetadata.get(\"id\");\r\n        ExtensionMetadata.set(ExtensionManager.extensions[id].installInfo.metadata);\r\n        \r\n        // Flag INSTALL_COMPLETE\r\n        var prefs = BracketsPreferencesModel.get(\"preferenceSystem\");\r\n        if (!prefs.get(BracketsPreferencesModel.INSTALL_COMPLETE)) {\r\n            prefs.set(BracketsPreferencesModel.INSTALL_COMPLETE, true);\r\n\r\n            // Log bundled build info\r\n            var autoInstalledExtensions = BracketsPreferencesModel.getViewState(\"auto-install-extensions\") || {},\r\n                bundledBuildVersion = autoInstalledExtensions[ExtensionMetadata.get(\"id\")],\r\n                isBundledBuild = !!bundledBuildVersion;\r\n\r\n            graphite.metricsProxyModel.setDefaultParam(\"isBundledBuild\", isBundledBuild);\r\n\r\n            if (isBundledBuild) {\r\n                graphite.metricsProxyModel.setDefaultParam(\"bundledBuildVersion\", bundledBuildVersion);\r\n\r\n                // Only set bundled build version on first install\r\n                prefs.set(BracketsPreferencesModel.INSTALL_BUNDLED_BUILD_VERSION, bundledBuildVersion);\r\n            }\r\n\r\n            // Trigger INSTALL_COMPLETE after ExtensionMetadata populates\r\n            graphite.events.trigger(\"install-complete\");\r\n            _isExtensionActive = true;\r\n        }\r\n\r\n        ParfaitConstants.Shortcut.HELP = 112; // F1 key\r\n        \r\n        // Create UI\r\n        MainPanel.registerMainPanel();\r\n        ParfaitPopup.initParfaitControlsPopup();\r\n\r\n        // TODO better event for detecting if extension is active?\r\n        $(Commands.get(Commands.TOGGLE_PANEL)).on(\"checkedStateChange\", function () {\r\n            _isExtensionActive = true;\r\n        });\r\n        \r\n        _attachExtensionManagerListener();\r\n        _attachListeners();\r\n        _setupAbandonmentEvents();\r\n\r\n        // Other global metrics\r\n        $(EditorManager).on(\"activeEditorChange\", function (event, current) {\r\n            _logEditorDocumentType(current);\r\n        });\r\n\r\n        // Initialize metrics\r\n        _saveDefaultParams();\r\n    }\r\n\r\n    // Initialize the extension after CSS loads and APP_READY fires\r\n    stylePromise.done(function () {\r\n        AppInit.appReady(_appReadyHandler);\r\n    });\r\n});\r\n\n//# sourceURL=/main.js");

