eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true, sub: true */\r\n/*global define: true, graphite: true, window: true, brackets: true*/\r\n\r\ndefine(\'override/models/BracketsMetricsProxyModel\',[\r\n    \'jquery\',\r\n    \'underscore\',\r\n    \'backbone\',\r\n    \'ccweb.files.extract/models/MetricsProxyModel\',\r\n    \'ext/Constants\',\r\n    \'ext/models/ExtensionMetadata\',\r\n    \"ext/models/BracketsPreferencesModel\"\r\n], function ($, _, Backbone, MetricsProxyModel, Constants, ExtensionMetadata, BracketsPreferencesModel) {\r\n    \"use strict\";\r\n\r\n    var WEBPA_PSDLENS_NOTAUTHORIZED = \"WEBPA_PSDLENS_NOTAUTHORIZED\",\r\n        WEBPA_PSDLENS_AUTHORIZED = \"WEBPA_PSDLENS_AUTHORIZED\",\r\n        WEBPA_PSDLENS_NAVIGATION = \"WEBPA_PSDLENS_NAVIGATION\",\r\n        WEBPA_PSDLENS_UPLOAD = \"WEBPA_PSDLENS_UPLOAD\",\r\n        WEBPA_PSDLENS_EXTRACT = \"WEBPA_PSDLENS_EXTRACT\",\r\n        WEBPA_PSDLENS_HELP = \"WEBPA_PSDLENS_HELP\";\r\n\r\n    var METRICS_FLUSH_INTERVAL_MINUTES = 5;\r\n\r\n    var BracketsMetricsProxyModel = MetricsProxyModel.extend({\r\n\r\n        defaults: {\r\n            metricsEnableComplete: false,\r\n            flushInterval: 0,\r\n            prefs: BracketsPreferencesModel.get(\"preferenceSystem\"),\r\n            \r\n            // See MetricsProxyModel.initialize() for graphite event names\r\n            eventMap: {\r\n                // extract-for-brackets events\r\n                \"install-complete\"              : {},\r\n                \"launch\"                        : {},\r\n                \"uninstall-complete\"            : {},\r\n                \"app-close\"                     : {flush: true, flushAsync: false},\r\n                \"project-open\"                  : {},\r\n                \"project-close\"                 : {},\r\n                \"hide-panel\"                    : {},\r\n                \"update-complete\"               : {},\r\n                \"ims-ready\"                     : {handler: \"imsReady\"},\r\n                \"expired-access-token\"          : {},\r\n                \"valid-access-token\"            : {},\r\n                \"missing-access-token\"          : {},\r\n                \"login-success\"                 : {},\r\n                \"login-cancel\"                  : {},\r\n                \"login-start\"                   : {},\r\n                \"logout-success\"                : {},\r\n                \"logout-failure\"                : {},\r\n                \"logout-attempted\"              : {},\r\n                \"browse\"                        : {flush: true, flushAsync: true},\r\n                \"browse-show-upload-files\"      : {flush: true, flushAsync: true},\r\n                \"layer-select\"                  : {},\r\n                \"css-hint-insert\"               : {event: WEBPA_PSDLENS_EXTRACT},\r\n                \"css-hint-show\"                 : {},\r\n                \"html-hint-insert\"              : {event: WEBPA_PSDLENS_EXTRACT},\r\n                \"html-hint-show\"                : {},\r\n                \"zoomChanged\"                   : {subevent: \"ZOOM_CHANGED\"},\r\n                \"layers-panel-toggle\"           : {},\r\n                \"layers-visibility-toggle\"      : {},\r\n                \"extract-asset-downloading\"     : {event: WEBPA_PSDLENS_EXTRACT},\r\n                \"extract-asset-downloaded\"      : {event: WEBPA_PSDLENS_EXTRACT},\r\n                \"extract-asset-canvas-fallback\" : {event: WEBPA_PSDLENS_EXTRACT},\r\n                \"extract-asset-canvas\"          : {event: WEBPA_PSDLENS_EXTRACT},\r\n                \"extract-asset-error\"           : {event: WEBPA_PSDLENS_EXTRACT},\r\n                \"how-to-dialog-opened\"          : {},\r\n                \"how-to-dialog-closed\"          : {},\r\n                \"first-launch-call-to-action\"   : {},\r\n                \"preferences-dialog-opened\"     : {},\r\n                \"preferences-dialog-closed\"     : {},\r\n                \"preference-changed\"            : {},\r\n                \"preview-rendition-finish\"      : {},\r\n                \"route-change\"                  : {handler: \"routeChange\"},\r\n                \"main-toolbar-expanded\"         : {},\r\n                \"main-toolbar-collapsed\"        : {},\r\n                \"tutorial-hide\"                 : {},\r\n                \"tutorial-select-layer\"         : {},\r\n                \"tutorial-code-hint\"            : {},\r\n                \"tutorial-measurement\"          : {},\r\n                \"tutorial-call-to-action\"       : {},\r\n                // projectparfait events\r\n                \"landing-page\"                  : {event : WEBPA_PSDLENS_NOTAUTHORIZED},\r\n                \"login-dialog-shown\"            : {event : WEBPA_PSDLENS_NOTAUTHORIZED, subevent : \"LOGIN_DIALOG\"},\r\n                \"upload-init\"                   : {event : WEBPA_PSDLENS_UPLOAD, subevent : \"INIT\", handler : \"uploadInit\"},\r\n                \"uploaded\"                      : {event : WEBPA_PSDLENS_UPLOAD, subevent : \"COMPLETE\"},\r\n                \"upload-failed\"                 : {event : WEBPA_PSDLENS_UPLOAD, subevent : \"FAILED\"},\r\n                \"processed\"                     : {event : WEBPA_PSDLENS_UPLOAD},\r\n                \"processing-failed\"             : {event : WEBPA_PSDLENS_UPLOAD},\r\n                \"drawPreviewStart\"              : {subevent: \"RENDER_START\"},\r\n                \"drawPreviewFinish\"             : {handler : \"drawPreviewFinish\"},\r\n                \"rendered\"                      : {handler : false},\r\n                \"selectedTab\"                   : {subevent : \"SELECTED_TAB\"},\r\n                \"help-dialog\"                   : {},\r\n                \"help-dialog-shortcut\"          : {},\r\n                \"first-user-overlay-shortcut\"   : {},\r\n                \"show-color-popup\"              : {},\r\n                \"show-measurement-offsets\"      : {},\r\n                \"copy-css\"                      : {},\r\n                \"copy-text\"                     : {},\r\n                \"copy-value\"                    : {},\r\n                \"color-format\"                  : {},\r\n                \"report-fidelity-error\"         : {subevent : \"FIDELITY_ERROR\"},\r\n                \"typekitSearch\"                 : {subevent : \"TYPEKIT_SEARCH\"},\r\n                \"extract-asset-download-init\"   : {subevent : \"ASSET_DOWNLOAD_INIT\"},\r\n                \"asset-extracted-scaled\"        : {},\r\n                \"canvasClick\"                   : {subevent : \"PSD_CANVAS_CLICKED\"},\r\n                \"canvasClickDrag\"               : {subevent : \"PSD_CANVAS_CLICKDRAG\"},\r\n                // undefined \"cssDoublClick\" /*confirmed typo*/,                : {subevent : \"CSS_PROPERTIES_DOUBLECLICKED\"},\r\n                \"register-defaults\"             : {handler: \"registerDefaults\"},\r\n                \"load-alert\"                    : {event : WEBPA_PSDLENS_NAVIGATION, subevent : \"ALERT_DIALOG\", handler : \"loadAlert\"},\r\n                \"load-calltoaction\"             : {event : WEBPA_PSDLENS_NOTAUTHORIZED, subevent : \"CALLTOACTION_DIALOG\"},\r\n                \"bad-file-type-dragdrop\"        : {event : WEBPA_PSDLENS_UPLOAD},\r\n                \"show-extract-asset-popup\"      : {handler : \"showExtractAssetPopup\"},\r\n                \"downloading-extracted-asset\"   : {},\r\n                \"featureFlagSet\"                : {event : WEBPA_PSDLENS_NAVIGATION, subevent : \"FEATURE_FLAG_SET\"},\r\n                \"layerCompShown\"                : {subevent : \"LAYER_COMP_SHOWN\"},\r\n                \"layerCompChanged\"              : {handler: \"layerCompChanged\"},\r\n                \"layer-comp-changed\"            : {}, // See MetricsProxyModel.layerCompChanged()\r\n                \"commitDropperColor\"            : {subevent : \"DROPPER_COLOR_EXTRACTED\"},\r\n                \"preprocessorChanged\"           : {subevent : \"CSS_PREPROCESSOR_CHANGED\"},\r\n                \"catchIntercept\"                : {subevent : \"CATCH_INTERCEPT\"},\r\n                \"baseFontSizeChanged\"           : {subevent : \"BASE_FONT_SIZE_CHANGED\"},\r\n                \"preferredFontUnitsChanged\"     : {subevent : \"PREFERRED_FONT_UNITS_CHANGED\"}\r\n            }\r\n        },\r\n\r\n        initialize: function () {\r\n            var self = this;\r\n            \r\n            window.graphite = window.graphite || {};\r\n            window.graphite.events = window.graphite.events || _.extend({}, Backbone.Events);\r\n            \r\n            // Handle changes to metrics opt-out\r\n            var prefs = this.get(\"prefs\");\r\n            prefs.getPreference(BracketsPreferencesModel.METRICS_ENABLED).on(\"change\", function () {\r\n                self._updateMetricsEnabled();\r\n            });\r\n            \r\n            self._updateMetricsEnabled();\r\n\r\n            // Update default params when changed\r\n            prefs.getPreference(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS).on(\"change\", function () {\r\n                self._updateDefaultParams();\r\n            });\r\n\r\n            // Restore default params\r\n            this._updateDefaultParams();\r\n\r\n            ExtensionMetadata.on(\"change:version\", function () {\r\n                self.setDefaultParam(\"extractExtensionVersion\", ExtensionMetadata.get(\"version\"));\r\n            });\r\n        },\r\n\r\n        _updateDefaultParams: function () {\r\n            var prefs = this.get(\"prefs\");\r\n            this.defaultParams = prefs.get(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS);\r\n        },\r\n\r\n        setDefaultParam: function (key, value) {\r\n            MetricsProxyModel.prototype.setDefaultParam.call(this, key, value);\r\n\r\n            // Save default params\r\n            var prefs = this.get(\"prefs\");\r\n            prefs.set(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS, this.defaultParams);\r\n        },\r\n        \r\n        _updateMetricsEnabled: function () {\r\n            var self = this,\r\n                eventMap = this.get(\"eventMap\"),\r\n                prefs = this.get(\"prefs\"),\r\n                metricsEnableComplete = this.get(\"metricsEnableComplete\"),\r\n                isEnabled = prefs.get(BracketsPreferencesModel.METRICS_ENABLED);\r\n\r\n            if (isEnabled && !metricsEnableComplete) {\r\n                // Flush metrics at an interval\r\n                var flushInterval = window.setInterval(graphite.serverAPI.flushMetrics, METRICS_FLUSH_INTERVAL_MINUTES * 60 * 1000);\r\n                this.set(\"flushInterval\", flushInterval);\r\n\r\n                Object.keys(eventMap).forEach(function (eventKey) {\r\n                    var info = eventMap[eventKey];\r\n\r\n                    // init default subevent names\r\n                    if (!info.subevent) {\r\n                        info.subevent = eventKey.toUpperCase().replace(/-/g, \"_\");\r\n                    }\r\n\r\n                    // look for custom event handler\r\n                    var handler = info.handler && self[info.handler];\r\n\r\n                    // handler=false implies a graphite event that does not exist\r\n                    if (handler !== false && (typeof handler !== \"function\")) {\r\n                        // use default handler\r\n                        handler = self._createEventHandler(eventKey);\r\n                    }\r\n\r\n                    if (handler) {\r\n                        info._handlerFn = handler;\r\n                        window.graphite.events.on(eventKey, handler, self);\r\n                    }\r\n                });\r\n            } else if (!isEnabled && metricsEnableComplete) {\r\n                // Flush metrics now\r\n                // Since metrics opt-out is a special case, the last metric\r\n                // that should be logged is preferences-dialog-opened\r\n                graphite.serverAPI.flushMetrics(true);\r\n                \r\n                // Remove timer\r\n                window.clearInterval(this.get(\"flushInterval\"));\r\n                \r\n                Object.keys(eventMap).forEach(function (eventKey) {\r\n                    var info = eventMap[eventKey];\r\n                    window.graphite.events.off(eventKey, info._handlerFn);\r\n                });\r\n                \r\n                // clear cache\r\n                BracketsPreferencesModel.reset(BracketsPreferencesModel.MIXPANEL_PENDING_REQUESTS);\r\n                BracketsPreferencesModel.reset(BracketsPreferencesModel.MIXPANEL_BUFFER);\r\n            }\r\n            \r\n            this.set(\"metricsEnableComplete\", isEnabled);\r\n        },\r\n\r\n        _createEventHandler: function (eventKey) {\r\n            return function (params) {\r\n                this.trackEvent(eventKey, params);\r\n            };\r\n        },\r\n\r\n        // Patch to remove PII assetID\r\n        drawPreviewFinish: function (psdModel) {\r\n            var currTime = Date.now();\r\n            var renditionDuration = currTime - psdModel.get(\"previewStarted\");\r\n            var eventParams = {\r\n                renditionDuration: renditionDuration,\r\n                fileSize: psdModel.get(\"size\")\r\n            };\r\n\r\n            //this.setDefaultParam(\'assetID\', psdModel.get(\"id\"));\r\n            this.trackEvent(\"rendered\", eventParams);\r\n        },\r\n        \r\n        imsReady: function (params) {\r\n            this.setDefaultParam(\"isOffline\", false);\r\n            this.trackEvent(\"ims-ready\", params);\r\n        },\r\n\r\n        // Override MetricsProxyModel\r\n        uploadInit: function (params) {\r\n            var self = this;\r\n            // Exclude filename\r\n            params.files.forEach(function () {\r\n                self.trackEvent(\'upload-init\');\r\n            });\r\n        },\r\n\r\n        // Override MetricsProxyModel\r\n        uploaded: function (params) {\r\n            // Remove PII\r\n            params.path = undefined;\r\n            params.fileName = undefined;\r\n\r\n            this.trackEvent(\'uploaded\', params);\r\n        },\r\n        \r\n        routeChange: function (route) {\r\n            var state;\r\n            \r\n            if (route === Constants.Routes.FIRST_LAUNCH) {\r\n                state = Constants.States.STATIC_COMP;\r\n            } else if (route === Constants.Routes.FOLDER) {\r\n                state = Constants.States.FILE_BROWSER;\r\n            } else if (route === Constants.Routes.ASSET) {\r\n                state = Constants.States.USER_COMP;\r\n            } else if (route === Constants.Routes.AUTHLESS_ASSET) {\r\n                state = Constants.States.CREATIVE_URL;\r\n            }\r\n            \r\n            // \"tutorial\" state is event-specific for\r\n            // how-to-dialog-opened and how-to-dialog-closed\r\n            \r\n            // \"login dialog\" state is event-specific for\r\n            // login-start, login-success, login-cancel\r\n            \r\n            if (state) {\r\n                this.setDefaultParam(\"state\", state);\r\n            }\r\n        },\r\n\r\n        setConfigModel: function (model) {\r\n            MetricsProxyModel.prototype.setConfigModel.call(this, model);\r\n\r\n            this.setDefaultParam(\"bracketsVersion\", brackets.metadata.version);\r\n            this.setDefaultParam(\"userAgent\", window.navigator.userAgent);\r\n            this.setDefaultParam(\"state\", \"not launched\");\r\n\r\n            // Remove PII $city\r\n            this.setDefaultParam(\"$city\", \"\");\r\n            this.setDefaultParam(\"isOffline\", \"unknown\");\r\n        },\r\n\r\n        trackEvent: function (eventName, eventParams) {\r\n            // Note, it should not be possible to enter this method when\r\n            // metrics are disabled. _updateMetricsEnabled() is setup to\r\n            // synchronously listen for changes to the METRICS_ENABLED pref.\r\n            // As a fail safe, we check the preference again here in case\r\n            // trackEvent() is called externally.\r\n            var prefs = this.get(\"prefs\");\r\n            if (!prefs.get(BracketsPreferencesModel.METRICS_ENABLED)) {\r\n                return;\r\n            }\r\n\r\n            var baseEvent =  WEBPA_PSDLENS_NOTAUTHORIZED,\r\n                eventInfo,\r\n                params = eventParams || {},\r\n                internalUser = false,\r\n                isAuthValid = !!this.authModel.isValid(),\r\n                isPaidAccount = false,\r\n                state = this.defaultParams[\"state\"];\r\n\r\n            // Verify the parameters can be serialized to JSON\r\n            try {\r\n                JSON.stringify(params);\r\n            } catch (err) {\r\n                console.error(err);\r\n                return;\r\n            }\r\n\r\n            // https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/804\r\n            // WEBPA_PSDLENS_AUTHORIZED now implies user comp or creative URL.\r\n            // Use isLoggedIn to determine if the user was authorized or not.\r\n            // WEBPA_PSDLENS_NOT_AUTHORIZED is only used during first launch.\r\n            if (state === Constants.States.USER_COMP ||\r\n                state === Constants.States.CREATIVE_URL) {\r\n                baseEvent = WEBPA_PSDLENS_AUTHORIZED;\r\n            }\r\n\r\n            if (isAuthValid) {\r\n                // This should all be moved to something that registers them as defaults in the Metrics Controller once the user\'s Authm Model is returned and valid.\r\n                //attach the adobeID\r\n                // PII - disable for PSD Lens\r\n                //params.adobeID = this.authModel.getAdobeId();\r\n                //track whether the e-mail originates from adobe.com, adobetest.com & 601t.com\r\n                internalUser = this.authModel.isAdobeInternalUser();\r\n                // Get the user\'s register date\r\n                // PII - disable for PSD Lens\r\n                //registerDate = this.authModel.getRegisterDate();\r\n                // PII - disable for PSD Lens\r\n                //graphite.serverAPI.registerPersistentParameters({ internalUser: params.internalUser, registerDate: this.authModel.getRegisterDate() });\r\n                \r\n                isPaidAccount = this.authModel.isPaidAccount();\r\n            }\r\n\r\n            // FIXME don\'t compute internalUser and isAuthValid on every event\r\n            // Move into login-success?\r\n            // Set super properties\r\n            params.internalUser = internalUser;\r\n            params.isLoggedIn = isAuthValid;\r\n            params.isPaidAccount = isPaidAccount;\r\n\r\n            // Lookup event based on eventName subevent\r\n            eventInfo = this.get(\"eventMap\")[eventName];\r\n\r\n            function successCallback(resp) {\r\n                //console.log(\"trackEventOnServer: \" + JSON.stringify(resp || \'success\'));\r\n            }\r\n\r\n            function errorCallback(resp) {\r\n                //console.log(\"trackEventOnServer: \" + JSON.stringify(resp || \'fail\'));\r\n            }\r\n\r\n            if (eventInfo) {\r\n                var mergedParams = this.mergeParams(params);\r\n\r\n                // timestamp\r\n                if (!mergedParams.timestamp) {\r\n                    var currentDate = new Date();\r\n                    mergedParams.timestamp = currentDate.getTime();\r\n                }\r\n\r\n                // TODO (jasonsj): firstUse as defined here doesn\'t seem approriate for PSD Lens\r\n                // define firstUse parameter. In this case, it\'s if the timestamp - registerDate < 60 minutes (3600000 milliseconds)\r\n                // mergedParams.firstUse = registerDate && (mergedParams.timestamp - mergedParams.registerDate < 3600000);\r\n\r\n                // subevent\r\n                mergedParams.subevent = eventInfo.subevent;\r\n\r\n                // eventname (subevent has already been embedded)\r\n                mergedParams.eventname = eventInfo.event || baseEvent;\r\n\r\n                // Now post our event\r\n                graphite.serverAPI.trackEvent(mergedParams, successCallback, errorCallback, this);\r\n\r\n                // Flush metrics\r\n                if (eventInfo.flush) {\r\n                    graphite.serverAPI.flushMetrics(eventInfo.flushAsync);\r\n                }\r\n            } else {\r\n                console.error(\"eventName \'\" + eventName + \"\' is undefined in eventMap\");\r\n            }\r\n\r\n        }\r\n    });\r\n\r\n    return BracketsMetricsProxyModel;\r\n});\r\n\n//# sourceURL=/./models/BracketsMetricsProxyModel.js");

