eval("/*\r\n * ADOBE CONFIDENTIAL\r\n *\r\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\r\n *\r\n * NOTICE:  All information contained herein is, and remains\r\n * the property of Adobe Systems Incorporated and its suppliers,\r\n * if any.  The intellectual and technical concepts contained\r\n * herein are proprietary to Adobe Systems Incorporated and its\r\n * suppliers and are protected by trade secret or copyright law.\r\n * Dissemination of this information or reproduction of this material\r\n * is strictly forbidden unless prior written permission is obtained\r\n * from Adobe Systems Incorporated.\r\n */\r\n\r\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50 */\r\n/*global define, brackets, graphite, Mustache, window */\r\n\r\ndefine(\'ext/views/MainToolbarView\',[\r\n    \"jquery\",\r\n    \"underscore\",\r\n    \"backbone\",\r\n    \"ext/command/Commands\",\r\n    \"ext/strings\",\r\n    \"text!ext/views/templates/mainToolbarTemplate.html\",\r\n    \"text!ext/html/login-call-to-action-callout.html\",\r\n    \"ext/models/BracketsPreferencesModel\",\r\n    \"cceco/CollabServiceAPI\",\r\n    \"ext/ims/IMSLibWrapper\",\r\n    \"ext/utils/StatusBarUtil\"\r\n], function ($, _, Backbone, Commands, Strings, MainToolbarTemplate, LoginCalloutTemplate,\r\n              BracketsPreferencesModel, CollabServiceAPI,\r\n              IMSLibWrapper, StatusBarUtil) {\r\n\r\n    \"use strict\";\r\n\r\n    var Dialogs             = brackets.getModule(\"widgets/Dialogs\"),\r\n        DefaultDialogs      = brackets.getModule(\"widgets/DefaultDialogs\"),\r\n        StringUtils         = brackets.getModule(\"utils/StringUtils\");\r\n\r\n    var prefs = BracketsPreferencesModel.get(\"preferenceSystem\");\r\n    \r\n    var timerTogglePanel,\r\n        $loginCallOutHTML;\r\n\r\n    var MainToolbarView = Backbone.View.extend({\r\n        \r\n        //--- Event Map\r\n        events: {\r\n            \'click .e4b-help-button\':                   \'_handleHelpButton\',\r\n            \'click #firstrun-upload-psd\':               \'_handlePSDUploadButton\',\r\n            \'click #sign_up\':                           \'_handleSignUp\'\r\n        },\r\n        \r\n        _handleHelpButton: function () {\r\n            Commands.execute(Commands.GUIDED_TUTORIAL);\r\n        },\r\n\r\n        _disableFirstLaunchHelperUI: function () {\r\n            // next time, don\'t show initial tutorial or expand the toolbar\r\n            prefs.set(BracketsPreferencesModel.HIDE_FIRST_LAUNCH_PSD, true);\r\n        },\r\n\r\n        // collapses the toolbar if currently expanded\r\n        _collapseExpandedToolbar: function () {\r\n            // collapse the expanded toolbar\r\n            if (this.$mainToolbar.hasClass(\'expanded\')) {\r\n                if (timerTogglePanel) {\r\n                    window.clearTimeout(timerTogglePanel);\r\n                }\r\n                this.$mainToolbar.removeClass(\'expanded\');\r\n            }\r\n        },\r\n\r\n        // initiates the first launch call to action\r\n        _handleCallToAction: function () {\r\n            var self = this;\r\n\r\n            graphite.events.trigger(\"first-launch-call-to-action\");\r\n\r\n            this._collapseExpandedToolbar();\r\n\r\n            // call back into the main panel to show the files view\r\n            IMSLibWrapper.getAccessToken().done(function () {\r\n                self._disableFirstLaunchHelperUI();\r\n\r\n                Commands.execute(Commands.SHOW_ASSETS_ROOT);\r\n            });\r\n        },\r\n        \r\n        // show Sign In Popup\r\n        _showSignInPopUp: function () {\r\n            var self = this;\r\n\r\n            if (!$loginCallOutHTML) {\r\n                $loginCallOutHTML = $(Mustache.render(LoginCalloutTemplate, Strings));\r\n                $loginCallOutHTML.appendTo(\"body\").hide();\r\n            }\r\n\r\n            var $mainPanel = $(\"#e4b-main-panel\");\r\n            var $loginCallOutButton = $mainPanel.find(\"#firstrun-upload-psd\");\r\n            var callToActionButtonOffset = $loginCallOutButton.offset();\r\n\r\n            // Show callout\r\n            $loginCallOutHTML.css(\"top\", callToActionButtonOffset.top + $loginCallOutButton.height());\r\n            $loginCallOutHTML.css(\"left\", callToActionButtonOffset.left);\r\n            $loginCallOutHTML.show();\r\n\r\n            // Animate in\r\n            // Must wait a cycle for the \"display: none\" to drop out before CSS transitions will work\r\n            setTimeout(function () {\r\n                $loginCallOutHTML.addClass(\"animateOpen\");\r\n            }, 0);\r\n\r\n            $loginCallOutHTML.find(\"button\").click(function () {\r\n            // Animate close\r\n                $loginCallOutHTML.addClass(\"animateClose\");\r\n                $loginCallOutHTML.one(\"webkitTransitionEnd\", function () {\r\n                    // Normally we\'d use AnimationUtils for this, but due to an apparent Chrome bug, calling .is(\":hidden\")\r\n                    // causes the animation not to play (even though it returns false and that early-exit branch isn\'t taken).\r\n                    $loginCallOutHTML.removeClass(\"animateClose\");\r\n                    $loginCallOutHTML.hide();\r\n                });\r\n\r\n                // Navigate to AssetListView and force login\r\n                self._handleCallToAction();\r\n            });\r\n\r\n        },\r\n        \r\n        _handlePSDUploadButton: function () {\r\n            //If user is already signed in, dont show the sign-in popup\r\n\r\n            var isSignedIn = IMSLibWrapper.isSignedInUser();\r\n\r\n            if (isSignedIn) {\r\n                this._handleCallToAction();\r\n            } else {\r\n                this._showSignInPopUp();\r\n            }\r\n        },\r\n        \r\n        // handle link to sign up for a new Adobe ID\r\n        _handleSignUp: function (event) {\r\n            IMSLibWrapper.signUp().done(function () {\r\n                Commands.execute(Commands.SHOW_ASSETS_ROOT);\r\n            });\r\n        },\r\n\r\n        _handlePanelHide: function () {\r\n            if ($loginCallOutHTML) {\r\n                $loginCallOutHTML.hide();\r\n            }\r\n        },\r\n        \r\n        //--- Interface methods\r\n\r\n        // show/hide the first-run toolbar controls\r\n        displayFirstRunSection: function (show) {\r\n            if (show) {\r\n                this.$mainToolbar.find(\".first-run-section\").show();\r\n            } else {\r\n                this.$mainToolbar.find(\".first-run-section\").hide();\r\n            }\r\n        },\r\n        \r\n        // configure the toolbar available with the Asset List view\r\n        displayAssetListToolbar: function () {\r\n            this.$mainToolbar.find(\".asset-list-view\").show();\r\n            this.$mainToolbar.find(\".psd-details-view\").hide();\r\n        },\r\n        \r\n        // configure the toolbar available with the PSD Details view\r\n        displayPSDDetailsToolbar: function () {\r\n            this.$mainToolbar.find(\".asset-list-view\").hide();\r\n            this.$mainToolbar.find(\".psd-details-view\").show();\r\n        },\r\n\r\n        // initialize the View\r\n        initialize: function () {\r\n            var self = this;\r\n\r\n            this.$mainToolbar = null;\r\n\r\n            this.render();\r\n\r\n            graphite.events.on(\"hide-panel\", self._handlePanelHide);\r\n\r\n            // Setup event listener for guided tutorial\r\n            var cmdGuidedTutorial = Commands.get(Commands.GUIDED_TUTORIAL);\r\n            cmdGuidedTutorial.on(\"checkedStateChange\", function () {\r\n                var $helpIcon = self.$el.find(\".e4b-help-button\");\r\n\r\n                if (cmdGuidedTutorial.getChecked()) {\r\n                    $helpIcon.addClass(\"active\");\r\n                } else {\r\n                    $helpIcon.removeClass(\"active\");\r\n                }\r\n            });\r\n        },\r\n\r\n        // render the View\r\n        render: function () {\r\n            // render the template with the localized strings\r\n            this.$el.html(Mustache.render(MainToolbarTemplate, Strings));\r\n\r\n            // remember DOM elements for later reference\r\n            this.$mainToolbar = this.$el.find(\"#get-started-panel\");\r\n            \r\n            return this;\r\n        },\r\n\r\n        remove: function () {\r\n            this.$el.empty();\r\n            return Backbone.View.prototype.remove.call(this);\r\n        }\r\n\r\n    });\r\n\r\n    return MainToolbarView;\r\n\r\n});\n//# sourceURL=/./views/MainToolbarView.js");

