        // Changed to always resolve if no HTTP error occurs\r\n                    // retVal=1 track call was successful\r\n                    // retVal=0 otherwise\r\n                    // Note that an HTTP error will always be rejected\r\n                    function (retVal, textStatus, jqXHR) {\r\n                        if (retVal === 0) {\r\n                            deferred.reject();\r\n                        } else {\r\n                            deferred.resolve();\r\n                        }\r\n                    }\r\n                    */\r\n                });\r\n            } else {\r\n                deferred.reject();\r\n            }\r\n\r\n            return deferred.promise();\r\n        },\r\n\r\n        _sendNextBatch: function (flushAsync) {\r\n            var self = this,\r\n                prefs = this.getPrefs(),\r\n                queue = prefs.get(BracketsPreferencesModel.MIXPANEL_PENDING_REQUESTS),\r\n                batch = queue.length && queue.shift();\r\n\r\n            this._sendBatch(batch, flushAsync).then(function () {\r\n                // Success, update preferences with modified queue\r\n                // Refresh the queue in case it was modified since the last batch fired\r\n                queue = prefs.get(BracketsPreferencesModel.MIXPANEL_PENDING_REQUESTS).slice(1);\r\n                prefs.set(BracketsPreferencesModel.MIXPANEL_PENDING_REQUESTS, queue);\r\n\r\n                // Resolve when queue is empty\r\n                if (queue.length === 0) {\r\n                    _batchDeferred.resolve();\r\n                } else {\r\n                    self._sendNextBatch(flushAsync);\r\n                }\r\n            }, function () {\r\n                // Failed, do not update preferences and wait for next flush attempt\r\n                _batchDeferred.reject();\r\n            });\r\n        },\r\n\r\n        _flushMetrics: function (buffer, flushAsync) {\r\n            // Create a batch from the buffer as a JSON string, then encode as base64\r\n            var prefs = this.getPrefs(),\r\n                pendingRequests = prefs.get(BracketsPreferencesModel.MIXPANEL_PENDING_REQUESTS),\r\n                data = buffer.length && window.btoa(JSON.stringify(buffer));\r\n\r\n            // Add to queue and save queue to prefs\r\n            if (data) {\r\n                pendingRequests.push(data);\r\n                prefs.set(BracketsPreferencesModel.MIXPANEL_PENDING_REQUESTS, pendingRequests);\r\n\r\n                // Clear the event buffer\r\n                prefs.set(BracketsPreferencesModel.MIXPANEL_BUFFER, []);\r\n            }\r\n\r\n            // Check if batch already in progress or empty queue\r\n            if (_batchDeferred || !pendingRequests.length) {\r\n                return;\r\n            }\r\n\r\n            // Prepare to send to mixpanel\r\n            _batchDeferred = new $.Deferred();\r\n            _batchDeferred.always(function () {\r\n                _batchDeferred = null;\r\n            });\r\n\r\n            this._sendNextBatch(flushAsync);\r\n        },\r\n\r\n        handleEulaAccepted: function (accepted) {\r\n            // TODO\r\n        },\r\n        \r\n        flush: function (flushAsync) {\r\n            var prefs = this.getPrefs(),\r\n                buffer = prefs.get(BracketsPreferencesModel.MIXPANEL_BUFFER);\r\n\r\n            // Encode current buffer as base64 and it to queue\r\n            // Note that the current buffer could be empty\r\n            this._flushMetrics(buffer, flushAsync);\r\n        },\r\n        \r\n        track: function (eventName, params) {\r\n            var prefs = this.getPrefs(),\r\n                useBuffer = this.useBuffer();\r\n\r\n            // Abort if params can not be base64 encoded\r\n            try {\r\n                window.btoa(JSON.stringify(params));\r\n            } catch (err) {\r\n                return;\r\n            }\r\n\r\n            if (useBuffer) {\r\n                var buffer = prefs.get(BracketsPreferencesModel.MIXPANEL_BUFFER),\r\n                    props = { token: mixpanel.get_config(\"token\"), time: params.timestamp };\r\n\r\n                // HACK: Get default super properties from mixpanel.track() (e.g.\r\n                //       browser, OS, screen size, etc. They aren\'t exposed in API.\r\n                props = _.extend(\r\n                    props,\r\n                    mixpanel._.info.properties(),\r\n                    mixpanel.cookie.properties(),\r\n                    params\r\n                );\r\n\r\n                // Buffer all events\r\n                buffer.push({\r\n                    event: params.eventname,\r\n                    properties: props\r\n                });\r\n\r\n                // mixpanel API caps batches at 50 events\r\n                // https://mixpanel.com/help/reference/http#batch-requests\r\n                if (buffer.length >= this.BUFFER_SIZE) {\r\n                    // When queue is full, do async flush\r\n                    this._flushMetrics(buffer, true);\r\n                } else {\r\n                    // Update the buffer\r\n                    prefs.set(BracketsPreferencesModel.MIXPANEL_BUFFER, buffer);\r\n                }\r\n            } else if (this.isEulaAccepted()) {\r\n                mixpanel.track(params.eventname, params);\r\n            } /* else {\r\n                // drop the event\r\n            } */\r\n\r\n        }\r\n\r\n    });\r\n    \r\n    return new MixpanelWrapper();\r\n});\n//# sourceURL=/./metrics/MixpanelWrapper.js");

